/************************************************************\
 *   To Top
\************************************************************/
.nsr-backtop {
	box-sizing: border-box;
	z-index: 100000;
	position: fixed;
	bottom: 32px;
	right: 32px;
	overflow: visible;
	text-indent: 100%;
	white-space: nowrap;
	visibility: hidden;
	-webkit-transition: all .1s;
	-moz-transition: all .1s;
	transition: all .1s;
	-webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
	-moz-box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
	box-shadow: 0 0 10px rgba(0, 0, 0, 0.05);
	border: 3px solid transparent;
	border-radius: 50%;
	color: black;
	display: -webkit-box;
	display: -moz-box;
	display: -ms-flexbox;
	display: -webkit-flex;
	display: flex;
	align-items: center !important;
	justify-content: center !important;
	cursor: pointer;

	&:hover {
		-webkit-transition: all .1s;
		-moz-transition: all .1s;
		transition: all .1s;
	}
}

#nsr_backtop #nsrBackTopArrow rect {
	z-index: 100001;
	-webkit-transition: all .1s;
	-moz-transition: all .1s;
	transition: all .1s;
	cursor: pointer;

	&:hover {
		color: white;
		-webkit-transition: all .1s;
		-moz-transition: all .1s;
		transition: all .1s;
	}
}

.nsr-backtop.nsr-backtop-is-visible {
	border-color: rgba(0, 0, 0, 0.12);
	background-color: rgba(0, 0, 0, 0.12);
	visibility: visible;
}

.nsr-backtop.nsr-backtop-is-visible:hover {
	border-color: rgba(0, 0, 0, 0.6);
	background-color: rgba(0, 0, 0, 0.6);
	visibility: visible;
}

.wp-admin .nsr-backtop {

	&:before {
		left: -39px;
		top: 14px;
	}
}

// Buttons Frontend
.nsr-backtop {

	&.small {
		height: 24px;
		width: 24px;

		&:before {
			font-size: 12px;
			height: 24px;
			width: 24px;
			left: -18px;
			top: 2px;
		}
	}

	&.medium {
		height: 36px;
		width: 36px;

		&:before {
			font-size: 18px;
			height: 36px;
			width: 36px;
			left: -26px;
			top: 2px;
		}
	}

	&.default {
		height: 48px;
		width: 48px;

		&:before {
			font-size: 24px;
			height: 48px;
			width: 48px;
			left: -35px;
			top: 3px;
		}
	}

	&.large {
		height: 60px;
		width: 60px;

		&:before {
			font-size: 36px;
			height: 60px;
			width: 60px;
			left: -48px;
			top: 0;
		}
	}

}

